*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.n{
    font-weight: bolder;
    font-family: Arial, Helvetica, sans-serif;
    text-decoration: none;
    font-style: italic;
}
.n:hover{
    color: red;
}
#ab{
    height: 50px;
    width: 100%;
    background-color: rgb(73, 219, 230);

    position: sticky;
    top: 0;
    justify-content: flex-start;
    box-shadow: 0px 2px 5px black ;
}
#ab>ol{
    height: 50px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: start;
    padding-left: 100px;

}
#ab>ol>li{
    height: 50px;
    width: auto;
    padding: 14px;
    list-style: none;
    font-size: large;
}
#ab>ol>li:hover{
    background-color: green;
    border-radius: 7px;
}
#ab1{
    height: 80px;
    width: 100%;
    display: flex;
    

}
#ab2{
    
    background: white;
    /* height: 200px; */
    width: 100%;
    display: flex;
}

#abc1{
    background-color: rgb(244, 232, 245);
    height:auto;
    width:18% ;
}
#abc2{
    background-color:white;
    /* height:200px; */
    width: 62%;
    height: auto;
    box-shadow: 0px -1px 20px rgb(66, 66, 66);

}
#abc4{
    background-color: rgb(244, 232, 245);
    height:auto;
    width:20%;
    box-shadow: inset 0px 0px 2px black;
}
#ab3{
    color: black;
    background-color: navajowhite;
    height: 300px;
    width: 100%;
}
#ab3>div>section{
    height: 2px;
    width: 100%;
    background-color: black;
    box-shadow: inset 1px 0px 5px black;
}
#footer{height: 250px;
    display: flex;
}
#footer>div{
    height: 270px;
    width: 25%;
    margin-top: 20px;
    margin-left: 20px;
}
#footer>div>p{
    margin-top: 5px;
}
#abcd1{
    background-color: rgb(103, 240, 109);
    height: 80px;
    width: 50%;
    padding-top: 30px;
    padding-left: 70px;
}
#abcd1>div{
    font-size: larger;
    font-weight: bolder;
    font-family: Arial, Helvetica, sans-serif;
}
#abcd2{
    background-color: rgb(103, 240, 109);
    height: 80px;
    width: 50%;
    padding-top: 30px;
    padding-left: 200px;
}
#abcd2>div>input{
    border-radius: 5px;
    height: 30px;
    width: 250px;
    border-color: blue;
}
#cont1{
    height: auto;
    width: auto;
    margin-left: 30px;
    margin-top: 35px;
    padding-right: 35px;
}
#cont2{
    height: auto;
    width: 100%;
    margin-top: 30px;
    padding-bottom: 20px;
    display: flex;
    /* background-color: black; */
}
.f{
    height: 50px;
    width: 120px;
    border: 2px solid black;
    border-radius: 5px;
    background-color: green;
    display: flex;
    justify-content: center;
    padding-top: 9px;
    font-size: large;
    margin-top: 10px;
    font-weight: bolder;
    font-size: x-large;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
    list-style: none;
}
.f:hover{
    
    background-color: yellow;
}
#f1{
    
    margin-left: 10%;
}
#f2{
    margin-left: 50%;
}
#burgar{
    display: none;
    position: absolute;
    top: 9px;
    left : 20px;
    cursor: pointer;
    margin-bottom: 2px;
}
.line{
    width: 30px;
    height: 4px;
    margin: 5px 5px;
    background-color: white;
    border-radius: 2px;
}
.l{
    color: green;
    background-color: red;
}
#drawer{
    display: none;
}
@media only screen and (max-width: 724px) {
    /* #ab>ol{
        flex-direction: column;
    } */
    
    #ab1{
        display: flex;
        /* flex-direction: column; */
        height: auto;
    }
    #abcd1{
        height: 50px;
        padding-top: 15px;
        width: 100%;
        padding-left: 90px;
    }
    #abcd2{
        height: 50px;
        width: 100%;
        padding-top: 10px;
        padding-left: 20px;
    }
    #abcd2>div>input{
        width: 80%;
    }
    #burgar{
        display: block;
    }
    #abc1{
        display: none;
    }
    #abc2{
        width: 100%;
        height: auto;
        padding-right: 2%;
        padding-left: 1%;
    }
  
    #abc4{
        display: none;
    }
    #ab3{
        height: auto;
        width: 100%;
    }
    #footer{
        flex-direction: column; 
        padding-left: 10%;
        width: 100%;
        height: min-content;
    }
    #footer>div{
        height: auto;
        width: 100%;
    }
    #ab{
        position: relative;
        display: block;
        overflow: auto;
    }
    #ab>ol{
        padding-left: 5px;
    }
    #cont2{
        width: 100%;
        padding-right: 20px;
    }
    .f{
        width: 110px;
    }
    #f2{
        margin-left: 40%;
    }
    #drawer{
        display: block;
        width: 0px;
        height: 0px;
    }
}